<template>
  <div id="advisory">
    <my-header :title="title"></my-header>
    <div class="advisory_content">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import myHeader from '../../components/myHeader'

export default {
  name: 'advisory',
  components: {
    myHeader
  },
  computed: {
    title: function () {
      return this.$store.state.CurrentTitle
    }
  },
  data () {
    return {
    }
  }
}
</script>

<style scoped>
  #advisory {
  }
  #advisory .advisory_content{
    position: fixed;
    top: 3rem;
    width: 100%;
    bottom: 0;
  }
</style>



